@import '../../index.scss';

.map__image {
  position: absolute;
  cursor: pointer;
  z-index: 1;
	transition: all 0.3s;

  &:hover {
    transform: scale(1.01);
		-webkit-filter: $image-shadow1;
	}
}

.map__image--dark {
	&:hover {
		-webkit-filter: $image-shadow3;
	}
}

.map__institutions {
	align-self: center;
	justify-self: center;
	grid-column-start: 1;
	grid-column-end: 21;
	grid-row-start: 1;
	grid-row-end: 8;
	width: 100%;
	animation: institutions 5s infinite alternate;

	@keyframes institutions {
		0% {
			transform: scale(1.01);
			-webkit-filter: $animation-shadow2;
		}
	}
}

.map__realEstate {
	grid-column-start: 24;
	grid-column-end: 33;
	grid-row-start: 1;
	grid-row-end: 7;
	width: 95%;
	animation: realEstate 5s infinite alternate;
	animation-delay: 1s; 

	@keyframes realEstate {
		0% {
			transform: scale(1.01);
			-webkit-filter: $animation-shadow2;
		}
	}
}

.map__crypto {
	align-self: center;
	grid-column-start: 1;
	grid-column-end: 10;
	grid-row-start: 10;
	grid-row-end: 17;
	width: 95%;
	animation: crypto 5s infinite alternate;
	animation-delay: 2s; 

	@keyframes crypto {
		0% {
			transform: scale(1.01);
			-webkit-filter: $animation-shadow2;
		}
	}
}

.map__communityCenter {
	align-self: center;
	justify-self: center;
	grid-column-start: 15;
	grid-column-end: 25;
	grid-row-start: 13;
	grid-row-end: 15;
	width: 95%;
	animation: community 5s infinite alternate;
	animation-delay: 3s; 

	@keyframes community {
		0% {
			transform: scale(1.01);
			-webkit-filter: $animation-shadow2;
		}
	}
}

.map__creatives {
	grid-column-start: 25;
	grid-column-end: 33;
	grid-row-start: 10;
	grid-row-end: 16;
	width: 95%;
	animation: creatives 5s infinite alternate;
	animation-delay: 4s; 

	@keyframes creatives {
		0% {
			transform: scale(1.01);
			-webkit-filter: $animation-shadow2;
		}
	}
}


